export default "import { WebVitals } from '@zyf2e/monitor-web-performance'\nimport { app } from 'src/App'\n\nconst wv = new WebVitals({\n    appId: 'monitor-demo',\n    version: '1.0.0',\n    immediately: true,\n    reportCallback: (metrics) => {\n        const store = app._store\n\n        const {\n            data: { name, value },\n        } = metrics\n\n        if (name === 'first-contentful-paint' || name === 'api-complete-time' || name === 'custom-contentful-paint') {\n            store.dispatch({\n                type: 'performance/calcScore',\n                payload: {\n                    name,\n                    value,\n                },\n            })\n        }\n\n        store.dispatch({\n            type: 'performance/addMetrics',\n            payload: metrics,\n        })\n    },\n    needCCP: true,\n    logFpsCount: 20,\n    hashHistory: true,\n})\n\nexport default wv\n";